* {
   --main-color: #252525;
   --main-color-light: #353535;
   --bg: linear-gradient(rgba(0, 0, 0, 0), var(--main-color)),
      url(/static/media/WarrenBuffett.6849661d.jpg) no-repeat center center;
   --bg-dark: linear-gradient(rgba(0, 0, 0, 0.25), var(--main-color)),
      url(/static/media/WarrenBuffett.6849661d.jpg) no-repeat center center;
   --main-font: 'Assistant', sans-serif;   
   /* 'EB Garamond', serif; */
   --text-font: "Roboto Slab", serif;
   --number-font: "Special Elite", cursive;
   margin: 0;
   padding: 0;
   -webkit-touch-callout: none; /* iOS Safari */
   -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
   -ms-user-select: none; /* Internet Explorer/Edge */
   user-select: none;
}

::-webkit-scrollbar {
   width: 3px;
}
::-webkit-scrollbar-track {
   background: var(--main-color);
}
::-webkit-scrollbar-thumb {
   background-color: grey;
   border-radius: 20px;
}

.App {
   width: 100%;
   background-color: var(--main-color);
   font: var(--main-font);
   font-weight: 600;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
}

.LoadingBG {
