}

/* Home Page TopProphet/TopPrediction Common CSS */
.TitleAndButtons {
   width: 100%;
   margin: 0 1.5vw;
   display: flex;
   justify-content: space-between;
   align-items: center;
   color: white;
}

@media screen and (max-width: 520px) {
   .TitleAndButtons {
      flex-direction: column;
      align-items: flex-start;
   }

   .TitleButtons {
      margin-top: 1vh;
   }
}

.TitleAndButtons h2 {
   color: white;
   margin: 0 0;
}

/* Material UI Button */
.MuiButton-outlined {
   border: 1.5px solid rgb(71, 71, 71) !important;
   margin: 0 5px !important;
   border-radius: 0% !important;
   color: white !important;
   transition: background 0.3s ease-in-out !important;
}

.MuiButton-root:hover {
   background: rgb(71, 71, 71) !important;
}

/* Home and Search Spin */
.ant-spin-dot-item {
   background-color: white;
}

.ant-spin-nested-loading {
   width: 100%;
}

.ant-spin-container {
   width: 100%;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
}

.ant-spin-container::after {
   background: transparent !important;
}

.ant-spin-blur {
   opacity: 0 !important;
}

.ant-spin-lg .ant-spin-dot {
   margin-top: 15vh !important;
   font-size: 60px !important;
}

.ant-spin-lg .ant-spin-dot i {
   width: 30px !important;
   height: 30px !important;
}

/* animations */
.animation_fade {
   -webkit-animation: animation_fade 1s;
   animation: animation_fade 1s;
}

@-webkit-keyframes animation_fade {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}

@keyframes animation_fade {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}


.Navbar {
   width: 100%;
   height: 55px;
   background: transparent;
   transition: background .5s ease-in-out;
   z-index: 1000;

   display: flex;

   list-style: none;
   position: fixed;
   font-size: 1.1rem;
}

.Navbar .animation_fade {
   -webkit-animation: animation_fade 3s;
   animation: animation_fade 3s;
}

.NavbarActive {
   background: #303030c5;
}

.Navbar ul {
   height: 100%;
   list-style: none;
   display: inline-block;
   display: flex;
   align-items: stretch;
}

.Navbar li {
   height: 100%;
   display: flex;
   align-items: center;
   text-decoration: none;
   color: white;
   padding: 0 2vw;
   transition: background-color 0.5s ease-in-out, color 0.3s ease-in-out;
}

.Navbar li:hover {
   background-color: white;
   color: black;
}

.NavLeft {
   width: 33%;
}

.SearchBarContainer {
   width: 34%;
   display: flex;
   justify-content: center;
   align-items: center;
}

.SearchBar {
   margin-left: 3%;
   width: 80%;
   max-width: 500px;
   height: 60%;
   align-self: center;
   border: 1px solid white;
   display: flex;
   align-items: center;
}

.SearchBar #SearchInput {
   width: 85%;
   height: 60%;
   background-color: transparent;
   color: white;
   border: none;
}

.SearchBar #SearchInput:focus {
   box-shadow: none;
}

.SearchBar .Icon {
   width: 15%;
   height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   color: white;
   transition: transform 0.2s ease-in-out;
}
.SearchBar .Icon:hover {
   transform: scale(1.2);
}

.NavRight {
   width: 33%;
   display: flex;
   justify-content: flex-end;
}

.Navbar .NavRight img {
   width: 35px;
   height: 35px;
   border-radius: 50%;
   margin-right: 8px;
}



/* Responsive NavBar Burger */
@media screen and (max-width: 768px) {
   .Navbar {
      justify-content: space-between;
   }

   .SearchBarContainer {
      width: 60%;
   }

   .Navbar ul {
      height: 5%;
      width: 60%;
   }
   .Navbar li {
      font-size: 1rem;
      width: 100%;
      border: 2px solid white;
      border-radius: 1px;
      margin-bottom: 2vh;
   }
}


.Burger {
   width: 15%;
}

.Burger li {
    height: 50px;
}

.Burger .UserInfo {
