.Cover .animation_fade_slide_top {
   -webkit-animation: animation_slide_top 2s
         cubic-bezier(0.25, 0.46, 0.45, 0.94) both,
      animation_fade 3s;
   animation: animation_slide_top 2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both,
      animation_fade 3s;
}

@-webkit-keyframes animation_slide_top {
   0% {
      transform: translateY(0);
   }
   100% {
      transform: translateY(-10vh);
   }
}
@keyframes animation_slide_top {
   0% {
      transform: translateY(0);
   }
   100% {
      transform: translateY(-10vh);
   }
}

.Cover .animation_bounce {
   -webkit-animation-name: bounce;
           animation-name: bounce;
   -webkit-animation-timing-function: ease;
           animation-timing-function: ease;
}
@-webkit-keyframes bounce {
   0% {
      transform: scale(1, 1) translateY(0);
   }
   10% {
      transform: scale(1.1, 0.9) translateY(0);
   }
   30% {
      transform: scale(0.9, 1.1) translateY(-25px);
   }
   50% {
      transform: scale(1.1, 0.9) translateY(0);
   }
   60% {
      transform: scale(0.9, 1.1) translateY(-12px);
   }
   80% {
      transform: scale(1, 1) translateY(0);
   }
   100% {
      transform: scale(1, 1) translateY(0);
   }
}
@keyframes bounce {
   0% {
      transform: scale(1, 1) translateY(0);
   }
   10% {
      transform: scale(1.1, 0.9) translateY(0);
   }
   30% {
      transform: scale(0.9, 1.1) translateY(-25px);
   }
   50% {
      transform: scale(1.1, 0.9) translateY(0);
   }
   60% {
      transform: scale(0.9, 1.1) translateY(-12px);
   }
   80% {
      transform: scale(1, 1) translateY(0);
   }
   100% {
      transform: scale(1, 1) translateY(0);
   }
}

